*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --white-color: #fff;
  --white-transparent-color: rgba(255, 255, 255, .5);
  --black-color: #141414;
  --dark-green-color: #1e2827;
  --dark-gray-color: rgba(20, 20, 20, .5);
  --light-gray-color: rgba(20, 20, 20, .4);
  --copy-color: #c6cdd1;
  --red: red;
  --cubic: cubic-bezier(.4, 0, .2, 1);
  letter-spacing: -.02em;
  color: var(--black-color);
  background-color: var(--white-color);
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-family: Epilogue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.28571;
}

.hero-section {
  background: var(--black-color);
  color: var(--white-color);
  padding-top: 122px;
}

.hero {
  width: 100%;
  flex-wrap: wrap;
  align-items: center;
  gap: 40px 20px;
  display: flex;
  position: relative;
}

.hero-title {
  max-width: 750px;
  letter-spacing: -.04em;
  order: 1;
  font-size: 60px;
  font-weight: 600;
  line-height: 1.03333;
}

.hero-text {
  max-width: 248px;
  order: 3;
  margin-left: auto;
  font-weight: 300;
}

.hero-top-slider {
  width: 436px;
  order: 2;
  position: relative;
}

.hero-slider {
  width: 100%;
  height: 255px;
  background: var(--dark-green-color);
  position: relative;
  overflow: hidden;
}

.hero-slider img {
  height: 100%;
  width: 100%;
}

.slider-arrows {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.swiper-arrows {
  width: 40px;
  height: 40px;
  border: 1px solid var(--white-color);
  opacity: .5;
  cursor: pointer;
  transition: opacity 250ms var(--cubic);
  border-radius: 8px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-right: 12px;
  display: flex;
}

.swiper-arrows svg {
  width: 24px;
  height: 24px;
}

.swiper-arrows:hover, .swiper-arrows:focus {
  opacity: 1;
}

.swiper-next {
  margin-right: auto;
}

.swiper-number {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.slide-first {
  align-items: center;
  display: flex;
}

.slide-first:after {
  content: "";
  width: 32px;
  height: 1px;
  background: var(--white-color);
  margin: 0 8px;
  display: block;
}

.hero-image {
  order: 4;
}

.hero-image img {
  width: 100%;
}

@media screen and (max-width: 767px) {
  .hero-title {
    max-width: 653px;
    font-size: 38px;
    line-height: 1.05263;
  }

  .hero-top-slider {
    width: 266px;
  }

  .hero-slider {
    height: 200px;
