.card {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform .8s;
  position: relative;
}

.card:hover, .card:focus {
  transform: rotateY(180deg);
}

.front, .back {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
}

.front {
  justify-content: center;
  align-items: center;
  display: flex;
}

.back {
  color: #fff;
  background: #1e2827;
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  padding: 30px 26px;
  display: flex;
